a{
    text-decoration: none;
    color: #333;
}

.btn{
    position: absolute;
    left: 200px;
    top: 0px;
    width: 140px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: rgb(59, 59, 59);
    background-color: rgba(255, 255, 255, 0.7);
    font-weight: bold;
}

nav{
    position: absolute;
    left: 200px;
    top: 50px;
    width: 140px;
    height: 150px;
    transition: all 1s; 
}

    nav.act{
        left: 340px;
        background: #555;
        transition: all 1s;
    }

.menu_01, .menu_02, .menu_03{
    position: relative;
    clear: both;
    width: 140px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #333;
    cursor: pointer;
    background-color:rgba(159, 203, 245, 0.6);
    transition: all 1s;
}

.menu_01:hover, .menu_02:hover, .menu_03:hover{
    color: #fff;
    background-color: rgba(16, 128, 232, 0.6);
    transition: all 1s;
    font-weight: bold;
}

.menu_01:hover > a, .menu_02:hover > a, .menu_03:hover > a{
    color: #fff;
}